    .c-drawer-enter-active .c-drawer__title, .c-drawer-enter-done .c-drawer__title, .c-drawer--has-title.c-drawer-enter-done .c-drawer__title, .c-drawer--has-title.c-drawer-enter-active .c-drawer__title {
