  width: 100%;
  align-items: center; }
  .c-radio-group--horizontal .c-radio-group__label {
    display: block;
    padding-right: 1em; }

.c-radio-group--hide-native .c-radio {
  padding: 8px;
  border: 1px solid transparent;
  border-radius: 3px;
  -webkit-tap-highlight-color: transparent; }
  .c-radio-group--hide-native .c-radio--has-focus {
    border-color: darkblue;
    border-color: var(--focus-border-color, darkblue); }
  .c-radio-group--hide-native .c-radio--checked {
