.l-playing {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: auto auto minmax(0, 1fr);
  overflow: hidden;
  position: fixed; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

